import axios from 'axios'; // axios
import Vue from "vue"
Vue.prototype.$http = axios;
// 
const baseUrl = "http://124.223.48.82:8080";
const localPath = ""
// const baseUrl = "http://127.0.0.1:4523/m1/2382127-0-default"
axios.defaults.headers.post['Content-Type'] = 'application/json';
axios.defaults.headers.put['Content-Type'] = 'application/json';
axios.defaults.timeout = 10000;

axios.interceptors.request.use(config => {
  return config;
});


/**
 * getget
 * @param {String} url [url]
 * @param {Object} params []
 */
export function get(url, params){    
    return new Promise((resolve, reject) =>{        
        axios.get(baseUrl + url, {            
            params: params        
        }).then(res => {
            resolve(res.data);
        }).catch(err =>{
            reject(err.data)        
    })    
});}

/** 
 * postpost 
 * @param {String} url [url] 
 * @param {Object} params [] 
 */
export function post(url, params) {
    return new Promise((resolve, reject) => {
         axios.post(baseUrl + url, params)
        .then(res => {
            resolve(res.data);
        })
        .catch(err =>{
            reject(err.data)
        })
    });
}
/** 
 * postpost 
 * @param {String} url [url] 
 * @param {Object} params [] 
 */
export function put(url, params) {
    return new Promise((resolve, reject) => {
         axios.put(baseUrl + url, params)
        .then(res => {
            resolve(res.data);
        })
        .catch(err =>{
            reject(err.data)
        })
    });
}

/**
 * localGetget
 * @param {String} url [publicurl/map]
 */
export function localGet(url){    
    return new Promise((resolve, reject) =>{        
        axios.get(localPath + url).then(res => {
            resolve(res.data);
        }).catch(err =>{
            reject(err.data)        
    })    
});}
