<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="20">
        <el-col :span="8" :offset="0">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getList"
          >
            <el-button
              icon="el-icon-search"
              slot="append"
              @click="getList"
            ></el-button>
          </el-input>
        </el-col>
      </el-row>
      <el-table
        :data="list"
        stripe
        border
        :header-cell-style="{ textAlign: 'center' }"
        :cell-style="{ 'text-align': 'center' }"
      >
        <el-table-column type="index"> </el-table-column>

        <!-- <el-table-column prop="name" label="" width="200">
        </el-table-column> -->
        <el-table-column prop="nickName" label="" width="200">
        </el-table-column>
        <el-table-column prop="phone" width="200" label="">
        </el-table-column>
        <el-table-column label="" width="150">
          <template slot-scope="scope">
            {{ scope.row.sex === "1" ? "" : "" }}
          </template>
        </el-table-column>
        <el-table-column prop="createTime" label=""></el-table-column>
        <el-table-column
          prop="updateTime"
          label=""
        ></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-switch
              active-text=""
              v-model="scope.row.status"
              @change="stateChanged(scope.row)"
            ></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="resetToZero(scope.row)"
              ></el-button
            >
            <el-button type="primary" size="mini" @click="deleteUser"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.page"
        :page-sizes="[5, 10, 15]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->

    <!-- <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editFormClosed"
    >
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="editForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="editForm.phone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sex">
          <template>
            <el-radio v-model="editForm.sex" label="1"></el-radio>
            <el-radio v-model="editForm.sex" label="0"></el-radio>
          </template>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="updateEdit"> </el-button>
      </span>
    </el-dialog> -->
  </div>
</template>
<script>
import { getUserList, updataUser,toZero } from "@/api";
export default {
  data() {
    // 
    var checkMobile = (rule, value, callback) => {
      const regMobile =
        /^(?:(?:\+|00)86)?1(?:(?:3[\d])|(?:4[5-79])|(?:5[0-35-9])|(?:6[5-7])|(?:7[0-8])|(?:8[\d])|(?:9[1589]))\d{8}$/;
      if (regMobile.test(value)) {
        callback();
      }
      callback(new Error(""));
    };
    return {
      queryInfo: {
        name: "",
        page: 1,
        pageSize: 5,
      },
      total: 1,
      list: [],
      //   
      editDialogVisible: false,
      //   
      userForm: {
        id: "",
        name: "",
        username: "",
        password: "",
        phone: "",
        sex: "",
        idNUmber: "",
        status: "",
      },
      //   
      editForm: {
        username: "",
        password: "",
        sex: "",
      },
      //   
      editFormRules: {
        username: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: " 3  10 ",
            trigger: "blur",
          },
        ],
        phone: [
          { required: true, message: "", trigger: "blur" },
          {
            validator: checkMobile,
            trigger: "blur",
          },
        ],
        sex: [{ required: true, message: "", trigger: "blur" }],
      },
    };
  },
  methods: {
    // 
    async getList() {
      const res = await getUserList(this.queryInfo);
      if (res.code === 0) return this.$message.error(res.msg);

      this.list = res.data.records;
      for (let i of this.list) {
        i.status = !!i.status;
      }
      this.total = res.data.total;
    },
    // 
    async stateChanged(user) {
      this.getEditForm(user);
      this.updateEditForm();
    },
    // 
    editInfo(user) {
      this.editDialogVisible = true;
      this.getEditForm(user);
      this.editForm = this.userForm;
    },
    // 
    editFormClosed() {
      this.$refs.editFormRef.resetFields();
    },
    // 
    updateEdit() {
      this.$refs.editFormRef.validate((valid) => {
        if (!valid) return;
        this.updateEditForm();
        this.editDialogVisible = false;
      });
    },
    // 
    getEditForm(user) {
      const { id, name, username, password, phone, sex, idNUmber, status } =
        user;
      this.userForm = {
        id,
        name,
        username,
        password,
        phone,
        sex,
        idNUmber,
        status,
      };
    },
    // 
    async updateEditForm() {
      const res = await updataUser(this.userForm);
      console.log(res);
      // if (res.code === 0) return this.$message.error("");
      this.$message.success("");
      this.getList();
    },
    deleteUser() {
      this.$message.info("");
    },
    //   
    handleSizeChange(newPageSize) {
      this.queryInfo.pageSize = newPageSize;
      this.getList();
    },
    //   
    handleCurrentChange(newPage) {
      this.queryInfo.page = newPage;
      this.getList();
    },
    // 
    async resetToZero(user){
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
          center: true
        }).then(async () => {
          const res = await toZero(user.id);
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
    }
  },
  created() {
    this.getList();
  },
};
</script>
<style scoped>
#sexItem span {
  margin: 10px;
}
</style>